import { setInterval, setTimeout } from "timers";
function frame(toGo: any) {
  if (toGo != undefined) {
    if (document.documentElement.scrollTop != toGo) {
      console.log(toGo);
      document.documentElement.scrollTop += 1;
    }
  }
}
export default Vue.extend({
  name: "Jumbotron",
  props: {
    content: Object
  },
  created() {},
  methods: {
    scrollToStart: function(toSection: String) {
      let scrollOffset = document.getElementById('header')!.clientHeight;
      window.scroll({
        top: this.$el.clientHeight - scrollOffset,
        left: 0,
        behavior: "smooth"
      });
      console.log(this.$root)
    }
  }
});
